* {
    box-sizing: border-box;
}

body {
    margin: 0;
    font-family: 'Poppins', sans-serif;
}

html,
h1,
h2,
h3,
h4,
h5,
h6,
p,
div {
    padding: 0;
    margin: 0;
}

nav {
    height: 55px;
    background-color: #F55A5A;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
}

.navbar--logo {
    height: 24px;
    width: 24px;
    margin-right: 10px;
}

.navbar--text {
    font-size: 16px;
    text-transform: lowercase;
    font-weight: 200;
}

main {
    padding: 50px 40px;
    width: 75%;
    min-width: 550px;
    margin: 0 auto;
}

.travel--section{
    display: flex;
    width: 100%;
    height: 250px;
    padding: 20px;
    flex-wrap: wrap;
    margin-bottom: 10px;
}

.travel--section > img {
    width: 40%;
    height: 100%;
}

.travel--section--details {
    padding: 15px 20px;
    width: 60%;
}

.location > img {
    height: 18px;
    width: 18px;
    margin-right: 10px;
}

.country {
    font-size: 18px;
    text-transform: uppercase;
    letter-spacing: 5px;
    margin-right: 15px;
    margin-bottom: 10px;
}

.maps--link {
    color: #918E9B;
}

.travel--section--details > h2 {
    font-size: 40px;
    font-weight: 700;
    margin-bottom: 18px;
}

.travel--section--details > h4 {
    font-size: 18px;
    margin-bottom: 10px;
}

.travel--description {
    font-size: 16px;
    line-height: 20px;
}

.divider {
    border: 3px solid #F5F5F5;
    background-color: #F5F5F5 ;
    flex-basis: 100%;
    margin-top: 20px;
}

@media only screen and (max-width: 1440px) {
    .travel--section {
        height: 350px;
    }
}

@media only screen and (max-width: 900px) {
    main {
        width: 90%;
    }

    .location > img {
        height: 16px;
        width: 16px;
        margin-right: 5px;
    }
    .travel--section {
        height: 300px;
    }
    .country {
        font-size: 13px;
        letter-spacing: 5px;
        margin-right: 13px;
        margin-bottom: 10px;
    }
    
    .maps--link {
        color: #918E9B;
        font-size: 13px;
    }
    
    .travel--section--details > h2 {
        font-size: 25px;
        font-weight: 700;
        margin-bottom: 15px;
    }
    
    .travel--section--details > h4 {
        font-size: 13px;
        margin-bottom: 10px;
    }
}

@media only screen and (max-width: 900px) {
    main {
        width: 95%;
    }
    .travel--section {
        height: 250px;
    }
    .country {
        font-size: 13px;
        letter-spacing: 5px;
        margin-right: 13px;
        margin-bottom: 10px;
    }
    
    .maps--link {
        color: #918E9B;
    }
    
    .travel--section--details > h2 {
        font-size: 30px;
        font-weight: 700;
        margin-bottom: 15px;
    }
    
    .travel--section--details > h4 {
        font-size: 13px;
        margin-bottom: 10px;
    }
}

@media only screen and (max-width: 768px) {
    .travel--description {
        font-size: 10px;
        line-height: 15px;
    }
}

